import { defineStore } from 'pinia';
import fetchApiStore from './fetchApi';

const fetchApifunction = fetchApiStore();
const { fetchApi } = fetchApifunction;

export default defineStore('beautyConsumable Store', {
  state: () => ({
    beautyConsumableData: [],
    beautyConsumableByCourse: [],
  }),
  actions: {
    // GET
    /** 取得所有美容耗材 */
    async getAllBeautyConsumable() {
      const url = `/api/BeautyConsumable/GetAll`;

      const response = await fetchApi(url);

      this.beautyConsumableData = response.data;
    },
    /** 取得療程對應的美容耗材 */
    async getBeautyConsumableByCourse(courseId) {
      const url = `/api/BeautyConsumable/GetByCourse?courseId=${courseId}`;

      const response = await fetchApi(url);

      this.beautyConsumableByCourse = response.data;
    }
  }
});