import { defineStore } from 'pinia';
import alertMessageStore from '@/store/alertMessage';
// import router from "@/router";

const alertMessage = alertMessageStore();
const { updateMessage } = alertMessage;

const BASE_URL = `${process.env.VUE_APP_API_URL}`;

export default defineStore('fetchApi Store', {
  state: () => ({}),
  actions: {
    async fetchApi(url, defaultOptions) {

      const headersJudge = defaultOptions && defaultOptions.headers;
      const methodJudge = defaultOptions && defaultOptions.method;
      const dataJudge = defaultOptions && defaultOptions.data;

      // const token = `eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9..l8LdA13fgjjeV52AcU8cHxMFznUtMQjb6ebLc3UNJUE`;

      const options = {
        headers: headersJudge ? defaultOptions.headers : {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${this.getTokenFromCookie()}`,
          // "Authorization" : `Bearer ${token}`,
        },
        method: methodJudge ? defaultOptions.method : 'GET',
      }

      if (dataJudge) {
        options.body = JSON.stringify(defaultOptions.data);
      }

      try {
        const response = await fetch(BASE_URL + url, options)
        .then(res => res.json())
        .then(res => res);

        const isGetJudge = options.method === 'GET' || options.method === 'get';

        // 只有 statusCode 200、非 GET 時，才會顯示訊息
        if (response.statusCode === 200 && !isGetJudge) {
          updateMessage(response.message);
        }
        
        // 只有不是 statusCode 200、非 GET 時，才會顯示訊息
        if (response.statusCode !== 200 && !isGetJudge) {
          updateMessage(`${response.message} ${response.statusCode}`);
        }
    
        return response;
      }
      catch (error) {
        // console.log(error);
        // router.push('/loginIndex');
      }
    },
    getTokenFromCookie() {
      const cookies = document.cookie.split(';');
      for (let i = 0; i < cookies.length; i++) {
        const cookie = cookies[i].trim();
    
        if (cookie.startsWith('token=')) {
          const token = cookie.substring('token='.length);
    
          return token;
        }
      }
    
      return null;
    }
  }
});