<script>
import { Modal } from 'bootstrap';
import { storeToRefs } from 'pinia';
// import { inject, toRefs, reactive, nextTick, ref, onMounted, computed, watch } from 'vue';
import { inject, toRefs, reactive, nextTick, computed, onMounted, ref } from 'vue';

import beautyConsumableStore from '@/store/beautyConsumable.js';

export default {
  name: 'dateOfAppointment',
  setup() {
    const beauticianRelation = beautyConsumableStore();

    // state --------------------------------------
    const { beautyConsumableByCourse } = storeToRefs(beauticianRelation);

    // action -------------------------------------
    const { getBeautyConsumableByCourse } = beauticianRelation;

    const checkIn = inject('checkIn');
    const alertMessage = inject('alertMessage');
    const textConversionTool = inject('textConversionTool');
    const { 
            state,
            getDaysOutpatientData,
            getClinicPeriodLastTime, 
            convertTimeFormatSlash,
            courseEnter,
            continuousOrReservedToggle,
          } = checkIn;

    const {
      clinicPeriodToCh,
      convertTimeFormatDeleteSecond,
      deleteTTimeFormat,
      } = textConversionTool;

    const { updateMessage } = alertMessage;

    const fetchFunction = inject('fetchFunction');

    const {
      fetchApi,
      getTokenFromCookie
    } = fetchFunction;

    const data = reactive({
      focusIndex: null,
      stripCalendar: [],
      historyCourseWindow: false,
    });

    const checkInModalRef = ref(null);

    let checkInModal;
    
    onMounted(() => {
      checkInModal = new Modal(checkInModalRef.value);
    })

    const displayConditions = (id) => {
      switch (id) {
        case 'IV':
          return false;
        case 'OPD':
          return false;
        default:
          return true;
      }
    }

    // --- 治療部位下拉選單 ---

    /** 治療部位下拉選單開關 */
    const bodyPartsWindow = ref(false);

    /** 開啟治療部位下拉選單 */
    const openAppointmentBodyPartsWindow = () => {
      if (bodyPartsWindow.value) return null;
      bodyPartsWindow.value = true;

      function close() {
        bodyPartsWindow.value = false;
        document.removeEventListener('click', close);
      }

      document.addEventListener('click', close);
    }

    const selectDate = ({date, month, year}) => {
      state.date = date;
      state.month = month - 1;
      state.year = year;

      getDaysOutpatientData();
    }

    const monthNumtoEn = (month) => {
      const arr = ["JAN", "FEB", "MAR", "APR", "MAY", "JUN", "JUL", "AUG", "SEP", "OCT", "NOV", "DEC"];
      return arr[month];
    }

    const appointmentBodyPartJudge = (appointmentBodyPart) => {

      if (!appointmentBodyPart) return false;
      
      let judge = false;
      const arr = appointmentBodyPart.split('、');
      arr.forEach(item => {
        if(item === '全臉'){
          judge = true;
        }
      });

      if (arr.length === 1 && judge || arr.length === 0) {
        return false;
      }

      return true;
    }

    const VIPJudge = (customerLevel) => {
      switch(customerLevel) {
        case 'Normal':
        return 'bg-white';
        case 'Gold':
        return 'bg-gold';
        case 'Purple':
        return 'bg-purple';
      }
    }

    /** 取得條狀拖拉日曆 */
    const getStripCalendarData = (year, month) => {
      let time = new Date(year, month+1, 1);
      let arr = [];
      let dayEn = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];

      time.setHours(time.getHours() - 1);    // 下個月 - 1 小時，目的是轉換成當月的最後一天 

      const allDate = time.getDate();

      for (let i = 1; i <= allDate; i++) {
        let index = new Date(year, month, i).getDay();
        arr.push({
          day: dayEn[index],
          date: i,
          month: month + 1,
          year: year,
        })
      }

      return arr;
    }

    const timeSwitch = (year, month, judge) => {
      if (judge) {
        month -= 1;
      } else {
        month += 1;
      }

      if (month === 12) {
        month = 0;
        year+= 1;
      }

      if (month === -1) {
        month = 11;
        year -= 1;
      }

      return { year, month };
    }

    const stripCalendarInit = () => {
      let currentMonth = getStripCalendarData(state.year, state.month);

      currentMonth.forEach((item, index) => {
        if (item.date === state.date) {
          data.focusIndex = index;
        }
      })

      const nextLen = currentMonth.length - 10;
      const lastLen = currentMonth.length - nextLen;

      if (data.focusIndex <= lastLen) {
        const { year, month } = timeSwitch(state.year, state.month, true);

        let lastMonth = getStripCalendarData(year, month);
        currentMonth = lastMonth.concat(currentMonth);
      }

      if (data.focusIndex >= nextLen) {
        const { year, month } = timeSwitch(state.year, state.month, false);
        let nextMonth = getStripCalendarData(year, month);
        currentMonth = currentMonth.concat(nextMonth);
      }

      currentMonth.forEach((item, index) => {
        if (item.date === state.date && item.month === state.month + 1 && item.year === state.year) {
          data.focusIndex = index;
        }
      })

      data.stripCalendar = currentMonth;
      
      calendarListFocus(data.focusIndex);
    }

    /** 條狀拖拉日曆移動到目標 */
    const calendarListFocus = (num) => {
     
      const focus = num - 5;

      nextTick(() => {
        const items = document.querySelectorAll('.calendarList');
        let total = 0;

        for (let i = 0; i < focus; i++) {
          total += items[i].offsetWidth + 32;
      }

      const scroll = document.querySelector('.scroll');
      scroll.scrollTo({left: total, behavior: 'smooth'});
      });
    }

    /** 其他部位名稱轉陣列 */
    const otherPartNameToArr = (item) => {
      if(!item) return null;
      return item.split(',');
    }

    const AestheticMedicineBgJudge = (item, {id, time}) => {

      // 抓出跨時段的格子
      const timeRangeJudge = state.timeRange.findIndex(item1 => item1.time === time && item1.operatorId === id);

      // 抓出跨時段的格子，判斷是否離診
      const leftJudge = state.timeRange.findIndex(item1 => item1.time === time && item1.left && item1.operatorId === id);
      
      // 如果沒開診
      if (!item.open) {
        return 'bg-gray2';
      }

      // 如果主要約診離診，就和跨時段一起變灰
      if (leftJudge !== -1) {
        return 'bg-gray3';
      }

      // 有主要約診 和 有跨時段
      if (item.normalAppointment || timeRangeJudge !== -1) {

        const left = item.normalAppointment ? item.normalAppointment.left : null;

        if (item.otherAppointment === null) {
          return left ? 'bg-gray3' : 'bg-green';
        }

        if (Array.isArray(item.otherAppointment)) {
          return item.otherAppointment.length === 0 && left ? 'bg-gray3' : 'bg-green';
        }
      }

      // 沒有主要約診
      if (!item.normalAppointment) {
        // 有次要約診
        if (item.otherAppointment) {
          const otherAppointmentAllLeftJudge = item.otherAppointment.every(item1 => item1.left === true);
          // 次要約診都離診
          if (otherAppointmentAllLeftJudge) {
            return 'bg-gray3';
          } else {
            return 'bg-green';
          }
        }
        // 沒有主要約診 和 沒有次要約診
        return 'bg-white';
      }
    }

    const beautyBgJudge = (item, time) => {

      // 抓出跨時段的格子
      const timeRangeJudge = state.timeRange.findIndex(item1 => item1.time === time);

      // 抓出跨時段的格子，判斷是否離診
      const leftJudge = state.timeRange.findIndex(item1 => item1.time === time && item1.left);

      if (!item.open) {
        return {'bg-gray2': true};
      }

      if (leftJudge !== -1) {
        return {'bg-gray3': true};
      }

      if (item.normalAppointment || timeRangeJudge !== -1 || item.otherAppointment) {
        return {'bg-green': true};
      }

      if (!item.normalAppointment) {
        return {'bg-white': true};
      }
    }

    const otherAppointmentBgJudge = (item, normalAppointment) => {
      if (!normalAppointment) {
        if (item.left) return 'bg-gray3';

        return 'bg-transparent'
      }

      return item.left ? 'bg-gray3' : 'bg-green'
    }

    const showDetails = (id) => {
      let dom = document.querySelector(`.index${id}`);
      dom.classList.remove('d-none');
    }

    const closeDetails = (id) => {
      let dom = document.querySelector(`.index${id}`);
      dom.classList.add('d-none');
    }

    /** 增加條狀拖拉日曆長度和下個顯示位置 */
    const calendarListMove = (judge) => {
      let len = data.stripCalendar.length;

      const nextLen = len - 10;
      const lastLen = len - nextLen;

      if (judge) {
        data.focusIndex = data.focusIndex - 6;
      } else {
        data.focusIndex = data.focusIndex + 6;
      }

      const focusItem = data.stripCalendar[data.focusIndex];

      if (data.focusIndex <= lastLen) {
        const { year, month } = timeSwitch(data.stripCalendar[data.focusIndex].year, data.stripCalendar[data.focusIndex].month - 1, true);

        let lastMonth = getStripCalendarData(year, month);

        data.stripCalendar = lastMonth.concat(data.stripCalendar);
      }

      if (data.focusIndex >= nextLen) {
        const { year, month } = timeSwitch(data.stripCalendar[data.focusIndex].year, data.stripCalendar[data.focusIndex].month - 1, false);
        let nextMonth = getStripCalendarData(year, month);
        data.stripCalendar = data.stripCalendar.concat(nextMonth);
      }

      data.stripCalendar.forEach((item, index) => {
        if (item.date === focusItem.date && item.month === focusItem.month && item.year === focusItem.year) {
          data.focusIndex = index;
        }
      })

      calendarListFocus(data.focusIndex);
    }

    const outpatientTimeJudge = (time) => {

      const arr = time.split(':');

      let timestamp = new Date().getTime();
      let itemTimestamp = new Date(state.year, state.month, state.date, parseInt(arr[0]), parseInt(arr[1])).getTime();

      return timestamp < itemTimestamp ? true : false;
    }

    const modalData = ref({});
    const modalFocusIndex = ref(0);

    /** 新增病歷用資料 */
    let medicalRecordInitData = {};

    /** 療程項目 - 選項 */
    const coursesOptions = ref([]);
    
    /** 治療部位 - 選項 */
    const bodyPartsOptions = ref([]);

    /** 治療部位 */
    const bodyParts = ref([]);

    const modalHeaderData = ref([]);

    /** 支付方式資料 */
    const paymentMethodData = ref([]);

    /** 支付方式下拉開關 */
    const paymentMethodSwitch = ref([]);

    /** 支付方式選項 */
    const paymentMethods = ref([]);

    /** 支付方式選項 - 其他部位 */
    const paymentMethodsForOtherBodyparts = ref([]);

    const activityData = ref([]);

    const cashData = ref([]);

    const voucherInput = ref([]);

    const oweVoucherInput = ref([]);

    /** 療程專用券 - 資料 */
    const voucherOrderDetailData = ref([]);

    const allBodyParts = ref([]);

    /** 其他部位 與 其他部位的數量 */
    const otherBodyPartLength = ref(1);
    const otherBodyParts = ref([]);

    let noDataForOtherParts = [];

    /** 總金額  */
    const total = ref(0);

    /** 病歷用的共用資料 */
    const commonDataList = ref({});

    /** 病歷用的共用資料 (其他部位) */
    const commonDataListForOhterBodypart = ref({});

    /** 收費內容(已登錄) */
    const chargeContent = ref({});

    /** 美容耗材下拉 */
    const beautyConsumables = ref([]);

    /** 取得新增病歷用資料 */
    const GetInitCreateMedicalRecordData = async(id) => {

      const date = `${state.year}-${state.month + 1 <= 9 ?'0':''}${state.month + 1}-${state.date <= 9 ?'0':''}${state.date}`;

      const url = `/api/MedicalRecord/GetInitCreateMedicalRecordData?departmentId=${state.department}&customerId=${id}&date=${date}`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      medicalRecordInitData = response.data;

      checkInModal.show();
    }

    /** 開啟到診登錄 */
    const openCheckInModal = async(customerData) => {
      await initCheckInModalData(customerData);

      const courseId = medicalRecordInitData.initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.courseId;

      updateBasicDataAndFee(customerData.customerId, courseId);
      checkInModal.show();
    }

    /** 初始/更新到診 modal 資料 */
    async function initCheckInModalData(customerData) {

      const { customerId, medicalRecordId, appointmentId } = customerData;

      await GetInitCreateMedicalRecordData(customerId);

      const { customer, initMedicalRecordDetails, commonData } = medicalRecordInitData;

      // 如果還未到診，就用 appointmentId 找資料
      initMedicalRecordDetails.forEach((item, i) => {
        if (item.notArrivedAppointmentIdList.length !== 0 && appointmentId) {
          item.notArrivedAppointmentIdList.forEach(item => {
            if (item === appointmentId) {
              modalFocusIndex.value = i;
              return null;
            }
          })

          return null;
        }

      // 到診，就用 medicalRecordId 找資料
        if (item.medicalRecordDetail.id === medicalRecordId && medicalRecordId) {
          modalFocusIndex.value = i;
        }
      })

      // 收費內容(bodyPart) - 已登錄
      const informationForbodyParts = [];

      let bodyPartIds = initMedicalRecordDetails[modalFocusIndex.value].bodyParts;

      // 美容部部位
      if (state.department === 'Beauty') {
        bodyPartIds = ['1'];
      }

      let bodyPart;

      // 點滴
      if (initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.courseType === 'IV') {
        bodyPartIds = [null];
      }

      bodyPartIds.forEach(itemA => {
        const couponOrderDetail = [];
        let coursePartOrderDetail = {};
        let voucherOrderDetail = '';

        if (itemA === 'Other') return null;

        bodyPart = initMedicalRecordDetails[modalFocusIndex.value].bodyPartsOptions.find(item => item.id === itemA);

        // 美容部部位
        if (state.department === 'Beauty') {
          bodyPart = { id: '1', name: '全臉' };
        }

        if (initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.courseType === 'IV') {
          bodyPart = { id: null, name: '點滴' };
        }

        const { name, id } = bodyPart;

        // 活動優惠券
        initMedicalRecordDetails[modalFocusIndex.value].couponOrderDetails.forEach(item => {
          if (itemA === item.bodyPartId) {
            const prizeItem = commonData.prizeData.find(itemB => itemB.prizeId === item.prizeId);
            
            couponOrderDetail.push({
              couponNumber: item.couponNumber,
              bodyPartId: item.bodyPartId,
              prizeName: `${prizeItem.activityName}_${prizeItem.prizeLevel}_${prizeItem.prizeName}`
            });
          }
        })

        // 現金支付
        initMedicalRecordDetails[modalFocusIndex.value].coursePartOrderDetails.forEach(item => {
          if (itemA === item.bodyPartId) {
            coursePartOrderDetail = item;
            const name = commonData.priceTypes.find(item => coursePartOrderDetail.priceType === item.id).name;
            coursePartOrderDetail.priceType = name;
          }
        })

        // initMedicalRecordDetails[modalFocusIndex.value].voucherOrderDetails.forEach(item => {
        //   if (itemA === item.bodyPartId) {
        //     if (!item.voucherId) {
        //       voucherOrderDetail = '未輸入';
        //       return null;
        //     } 
        //     voucherOrderDetail += `${item.voucherId}/`;
        //   }
        // })

        // if (voucherOrderDetail !== '未輸入') {
        //   voucherOrderDetail = voucherOrderDetail.substring(0, voucherOrderDetail.length - 1);
        // }

        // 療程專用卷
        initMedicalRecordDetails[modalFocusIndex.value].voucherOrderDetails.forEach(item => {
          if (itemA === item.bodyPartId) {
            voucherOrderDetail += `${item.voucherId} / `;
          }
        })

        let oweVoucher = 0;

        // 療程專用卷 - 欠卷
        initMedicalRecordDetails[modalFocusIndex.value].oweVoucherSituationData.forEach(item => {
          if (itemA === item.bodyPartId) {
            oweVoucher = item.oweAmount;
          }
        })

        if (oweVoucher === 0) {
          voucherOrderDetail = voucherOrderDetail.substring(0, voucherOrderDetail.length - 3);
        }

        
        informationForbodyParts.push({
          name,
          id,
          couponOrderDetail,
          coursePartOrderDetail,
          voucherOrderDetail,
          oweVoucher,
        });
      })

      // 收費內容(OtherBodyParts)
      const informationForOtherBodyParts = initMedicalRecordDetails[modalFocusIndex.value].coursePartOrderDetails
        .filter(item => item.bodyPartId === 'Other')
        .map(itemA => {
          itemA.priceType = commonData.priceTypes.find(itemB => itemA.priceType === itemB.id).name;
          return itemA;
        });

      // initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.anesthesiaSituation
      const anesthesiaSituation = commonData.anesthesiaSituations.find(item => item.id === initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.anesthesiaSituation);

      const anestheticOrderDetail = initMedicalRecordDetails[modalFocusIndex.value].anestheticOrderDetail;

      chargeContent.value = {
        anesthesiaSituationName: anesthesiaSituation ? anesthesiaSituation.name : null,
        anestheticOrderDetail: anestheticOrderDetail,
        informationForbodyParts,
        informationForOtherBodyParts,
        totalExpense: 0,
      }

      initMedicalRecordDetails[modalFocusIndex.value].coursePartOrderDetails.forEach(item => {
        chargeContent.value.totalExpense += parseInt(item.price);
      })


      commonDataList.value = commonData;
      commonDataListForOhterBodypart.value = JSON.parse(JSON.stringify(commonData));
      
      commonDataListForOhterBodypart.value.priceTypes = commonDataListForOhterBodypart.value.priceTypes.filter(item => item.id === 'Once' );

      // 複製療程種類 - 用於 modal header 初始
      const courseTypesData = JSON.parse(JSON.stringify(commonDataList.value.courseTypes));

      activityData.value = [];
      cashData.value = [];
      voucherInput.value = [];
      oweVoucherInput.value = [];
      voucherOrderDetailData.value = [];

      // modal 初始資料
      modalData.value = {
        name: customer.name,
        birthday: customer.birthday,
        beauticianNickname: customer.beauticianNickname,
        appointmentDate: initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.appointmentDate,
        appointmentTime: initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.appointmentTime,
        clinicPeriod: initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.clinicPeriod,
        arrivedTime: initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.arrivedTime,
        operator: initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.operator,
        courseType: initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.courseType,
        courseId: initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.courseId,
        anesthesiaType: commonDataList.value.anesthesiaTypes.find(item => item.id === initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.anesthesiaType),
        comment: initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.comment,
        anesthesiaSituation: initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.anesthesiaSituation,
        anestheticFee: commonDataList.value.anestheticFees[0].id,
        received: '',
        beautyConsumableId: '',
        notArrivedAppointmentIdList: initMedicalRecordDetails[modalFocusIndex.value].notArrivedAppointmentIdList.length === 0 ? false : true
      }

      otherBodyParts.value = initMedicalRecordDetails[modalFocusIndex.value].appointmentOtherParts;
      
      otherBodyPartLength.value = initMedicalRecordDetails[modalFocusIndex.value].appointmentOtherParts.length;

      if (!modalData.value.notArrivedAppointmentIdList) {
        modalData.value.operator = modalData.value.operator ? commonDataList.value.operators.find(item => item.operatorId === modalData.value.operator).operatorName : null;
        modalData.value.courseType = commonDataList.value.courseTypes.find(item => item.id === modalData.value.courseType).name;
        modalData.value.courseId = initMedicalRecordDetails[modalFocusIndex.value].courses.find(item => item.courseId === modalData.value.courseId).courseName;
        otherBodyParts.value = initMedicalRecordDetails[modalFocusIndex.value].appointmentOtherParts;
      }

      // 美容耗材下拉
      beautyConsumables.value = initMedicalRecordDetails[modalFocusIndex.value].beautyConsumables;
      
      // 下拉選項設置
      coursesOptions.value = initMedicalRecordDetails[modalFocusIndex.value].courses;
      bodyPartsOptions.value = initMedicalRecordDetails[modalFocusIndex.value].bodyPartsOptions;

      // 如果治療種類是 雷射 或 微整，選項列表就只顯示 雷射 和 微整
      if (modalData.value.courseType === 'Laser' || modalData.value.courseType === 'MicroPlasticSurgery') {
        commonDataList.value.courseTypes = commonDataList.value.courseTypes.filter(item => item.id === 'Laser' || item.id === 'MicroPlasticSurgery');
      }

      // 如果治療種類是 點滴，選項列表就只顯示 點滴
      if (modalData.value.courseType === 'IV') {
        commonDataList.value.courseTypes = commonDataList.value.courseTypes.filter(item => item.id === 'IV');
      }

      // 如果治療種類是 健保，選項列表就只顯示 健保
      if (modalData.value.courseType === 'OPD') {
        commonDataList.value.courseTypes = commonDataList.value.courseTypes.filter(item => item.id === 'OPD');
      }

      // modal header 初始
      modalHeaderData.value = initMedicalRecordDetails.map(item1 => {
        const courseType = courseTypesData.find(item2 => item2.id === item1.medicalRecordDetail.courseType).name;
        const course = item1.courses.find(item2 => item2.courseId === item1.medicalRecordDetail.courseId).courseName;

        return {
          courseType,
          course,
          medicalRecordId: item1.medicalRecordDetail.id,
          customerId,
          appointmentId: item1.notArrivedAppointmentIdList[0] ? item1.notArrivedAppointmentIdList[0] : null,
        }
      })

      // 治療部位
      bodyParts.value = initMedicalRecordDetails[modalFocusIndex.value].bodyPartsOptions.filter(item => initMedicalRecordDetails[modalFocusIndex.value].bodyParts.includes(item.id));

      let copyBodyParts = initMedicalRecordDetails[modalFocusIndex.value].bodyPartsOptions.filter(item => initMedicalRecordDetails[modalFocusIndex.value].bodyParts.includes(item.id));

      // 支付方式初始
      paymentMethodData.value = [];
      noDataForOtherParts = [];

      // 篩出 'Other'
      noDataForOtherParts = copyBodyParts.filter(item => item.id !== 'Other');

      // 美容部部位
      if (state.department === 'Beauty') {
        noDataForOtherParts.push({ id: '1', name: '全臉' });
      }

      if (modalData.value.courseType === 'IV') {
        noDataForOtherParts.push({ id: null, name: '點滴' });
      }

      // 治療部位 支付方式 初始資料
      noDataForOtherParts.forEach((item) => {

        paymentMethodData.value.push({
          bodyPartId: item.id,
          paymentMethod: [{id: 'Voucher', name: '療程專用券', sequence: 1, active: true}],
        })
        
        paymentMethodSwitch.value.push({
          bodyPartId: item.id,
          switch: false,
        })

        activityData.value.push([]);
        cashData.value.push([]);

        voucherInput.value.push('');
        oweVoucherInput.value.push({
          bodyPartId: item.id,
          oweAmount: 0
        });

        voucherOrderDetailData.value.push([]);
