          paymentMethod: [{ id: 'Cash', name: '現金支付', sequence: 3, active: true }],
        })

        paymentMethodSwitch.value.push({
          bodyPartId: "Other",
          switch: false,
        })

        // activityData.value.push([]);
        cashData.value.push([]);

        // voucherInput.value.push('');
        // voucherOrderDetailData.value.push([]);
      });

      // 治療部位 和 其他部位合併
      allBodyParts.value = [...noDataForOtherParts, ...otherBodyPartsCopy];

      for(let i = noDataForOtherParts.length; i < allBodyParts.value.length; i++) {
        cashData.value[i].push(
          {
            bodyPartId: allBodyParts.value[i].id,
            paymentMethodId: "Cash",
            priceType: "Once",
            price: allBodyParts.value[i].price,
            received: false,
            otherPartName: allBodyParts.value[i].part,
            otherPartScope: allBodyParts.value[i].scope,
            depositData: [],
          }
        );
      }

      // 支付方式列表
      paymentMethods.value = initMedicalRecordDetails[modalFocusIndex.value].paymentMethods;
      paymentMethodsForOtherBodyparts.value = JSON.parse(JSON.stringify(initMedicalRecordDetails[modalFocusIndex.value].paymentMethods));

      paymentMethodsForOtherBodyparts.value = paymentMethodsForOtherBodyparts.value.filter(item => item.id === 'Cash' || item.id === 'CreditCard' || item.id === 'Free')
      cashInputBlur();
    }

    /** modal header - 選擇病歷 */
    function selectMedicalRecord(item) {
      initCheckInModalData(item);
    }

    /** 如果其他被點，就開啟視窗 */
    const otherBodyPartJudge = computed(() => {
      let judge = false;
      if (bodyParts.value.length !== 0) {

        bodyParts.value.forEach(item => {
          if (item.id === 'Other') {
            judge = true;
          }
        })
      }

      return judge;
    });


    /** 開啟之前治療的方案 */
    const openHistoryCourseWindow = () => {
      if (data.historyCourseWindow) return null;
      data.historyCourseWindow = true;

      function close() {
        data.historyCourseWindow = false;
        document.removeEventListener('click', close);
      }

      document.addEventListener('click', close);
    }
    

    /** 新增其他部位的欄位 */
    const addOtherBodyPartLength = () => {
      otherBodyPartLength.value++;
      otherBodyParts.value.push({
        part: "",
        scope: "",
        price: ""
      })

      paymentMethodData.value.push({
        bodyPartId: "Other",
        paymentMethod: [{id: 'Cash', name: '現金支付', sequence: 3, active: true}],
      })

      paymentMethodSwitch.value.push({
        bodyPartId: "Other",
        switch: false,
      })

      allBodyParts.value.push({
        id: "Other",
        name: "其他",
        part: "",
        price: '',
        scope: ""
      });

      // activityData.value.push([]);
      cashData.value.push([]);

      // voucherInput.value.push('');
      // voucherOrderDetailData.value.push([]);

      cashData.value[cashData.value.length - 1].push(
        {
          bodyPartId: "Other",
          paymentMethodId: "Cash",
          priceType: "Once",
          price: null,
          received: false,
          otherPartName: '',
          otherPartScope: '',
          depositData: [],
        }
      );
    }

    function voucherTypesJudge(depositData, id, voucherType) {
      if (voucherType === id) return true;

      return !depositData.some(item => item.voucherType === id);
    }

    /** 刪除其他欄位 */
    const deleteOtherBodyPartLength = (index) => {
      otherBodyPartLength.value--;
      otherBodyParts.value.splice(index, 1);
      const newNoDataForOtherParts = bodyParts.value.filter(item => item.id !== 'Other');

      const len = newNoDataForOtherParts.length;
      const i = len + index;
      
      allBodyParts.value.splice(i, 1);

      paymentMethodData.value.splice(i, 1);
      paymentMethodSwitch.value.splice(i, 1);

      // activityData.value.splice(i, 1);
      total.value -= cashData.value[i][0].price;
      cashData.value.splice(i, 1);

      // voucherInput.value.splice(i, 1);
      // voucherOrderDetailData.value.splice(i, 1);
    }

    /** 刪除治療部位選項 */
    const deleteAppointmentBodyPartsItem = (item) => {
      let index = bodyParts.value.indexOf(item);
      // const newNoDataForOtherParts = bodyParts.value.filter(itemA => itemA.id !== 'Other');

      bodyParts.value.splice(index, 1);

      if (item.id !== 'Other') {
        // const deleteItem = allBodyParts.value.find(item => {
        //   return !newNoDataForOtherParts.includes(item);
        // });

        const i = allBodyParts.value.indexOf(item);

        allBodyParts.value.splice(i, 1);
        noDataForOtherParts.splice(i, 1);

        paymentMethodData.value.splice(i, 1);
        paymentMethodSwitch.value.splice(i, 1);

        activityData.value.splice(i, 1);

        let deleteAmount = 0;

        if (cashData.value[i].length !== 0) {
          if (cashData.value[i][0].priceType === 'Deposit') {
          cashData.value[i][0].depositData.forEach(item => {
            deleteAmount += item.amountOfMoney;
          });

            total.value -= deleteAmount;
          } else {
            total.value -= cashData.value[i][0].price;
          }
        }

        cashData.value.splice(i, 1);

        voucherInput.value.splice(i, 1);
        oweVoucherInput.value.splice(i, 1);
        voucherOrderDetailData.value.splice(i, 1);
      } else {
        otherBodyPartJudge.value = true;
        otherBodyPartLength.value = 0;
        otherBodyParts.value = [];
        for(let i = 0; i < allBodyParts.value.length; i++) {
          if (allBodyParts.value[i].id === "Other") {
            allBodyParts.value.splice(i, 1);

            paymentMethodData.value.splice(i, 1);
            paymentMethodSwitch.value.splice(i, 1);

            // activityData.value.splice(i, 1);

            total.value -= cashData.value[i][0].price;
            cashData.value.splice(i, 1);

            // voucherInput.value.splice(i, 1);
            // voucherOrderDetailData.value.splice(i, 1);

            i--;
          }
        }
      }
    }

    /** 修改療程類別，更新療程選項 */
    async function changeCourseType(courseType) {
      coursesOptions.value = await getCourseOptions(courseType);
      modalData.value.courseId = '';
      bodyParts.value = [];
      bodyPartsOptions.value = [];

      activityData.value = [];
      paymentMethodData.value = [];
      paymentMethodSwitch.value = [];

      cashData.value = [];
      voucherInput.value = [];
      oweVoucherInput.value = [];
      voucherOrderDetailData.value = [];
      allBodyParts.value = [];
      modalData.value.beautyConsumableId = '';

      modalData.value.anestheticFee = commonDataList.value.anestheticFees[0].id;

      total.value = 0;
    }

    /** 修改療程類別、療程選項，更新治療部位*/
    async function changeCourseId(courseId) {
      await getBeautyConsumableByCourse(courseId);

      beautyConsumables.value = beautyConsumableByCourse.value;

      bodyPartsOptions.value = await getBodyPart(courseId);
      bodyParts.value = [];

      activityData.value = [];
      paymentMethodData.value = [];
      paymentMethodSwitch.value = [];

      cashData.value = [];
      voucherInput.value = [];
      oweVoucherInput.value = [];
      voucherOrderDetailData.value = [];
      allBodyParts.value = [];
      modalData.value.beautyConsumableId = '';

      modalData.value.anestheticFee = commonDataList.value.anestheticFees[0].id;

      total.value = 0;
    }

    /** 開啟支付方式下拉選項 */
    function openPaymentMethodOption(index) {
      if (paymentMethodSwitch.value[index].switch) return null;
      
      let indexClicked;

      // 取得被展開過的優惠卷下拉視窗
      const judge = paymentMethodSwitch.value.find((item, i) => {
        if (item.switch) {
          indexClicked = i;
          return item;
        }
      })

      // 有被展開過，就關閉該視窗
      if (judge) {
        paymentMethodSwitch.value[indexClicked].switch = false;
        document.removeEventListener('click', close);
      }

      paymentMethodSwitch.value[index].switch = true;

      function close() {
        paymentMethodSwitch.value[index].switch = false;
        document.removeEventListener('click', close);
      }

      document.addEventListener('click', close);
    }

    /** 刪除已選的支付方式 */
    function deleteSelectedPaymentMethod(item, index) {

      const i = paymentMethodData.value[index].paymentMethod.indexOf(item);
      paymentMethodData.value[index].paymentMethod.splice(i, 1);

      if (item.id === 'Cash') {
        let deleteAmount = 0;

        if (cashData.value[index][0].priceType === 'Deposit') {
          cashData.value[index][0].depositData.forEach(item => {
            deleteAmount += item.amountOfMoney;
          });

          total.value -= deleteAmount;
          cashData.value[index] = [];
          return null;
        }

        total.value -= cashData.value[index][0].price;
        cashData.value[index] = [];
      }

      if (item.id === 'Voucher') {
        voucherOrderDetailData.value[index] = [];

        oweVoucherInput.value[index].oweAmount = null;
      }

      if (item.id === 'Activity') {
        activityData.value[index] = [];
      }
    }

    // --- 療程專用券 ---

    /** 療程專用券 - 輸入 */
    function voucherDataEnter(index) {
      voucherOrderDetailData.value[index].push({
        bodyPartId: allBodyParts.value[index].id,
        voucherId: voucherInput.value[index],
      });

      voucherInput.value[index] = '';
    }

    /** 療程專用券 - 刪除 */
    function deleteVoucherData(item, index) {
      const i = voucherOrderDetailData.value[index].indexOf(item);

      voucherOrderDetailData.value[index].splice(i, 1);
    }

    // --- 活動優惠券 ---

    /** 刪除活動優惠券的券號欄位 */
    function deleteActivityItem(index, i) {
      activityData.value[index].splice(i, 1);
    }

    /** 新增活動優惠券的券號欄位 */
    function addActivityItem(index) {
      activityData.value[index].push(
        {
          prizeId: commonDataList.value.prizeData[0].prizeId,
          bodyPartId: allBodyParts.value[index].id,
          couponNumber: ""
        }
      );
    }

    /** 改變現金支付種類 */
    function changePriceType(item, index) {

      total.value -= item.price;
      cashData.value[index][0].price = null;

      if (item.priceType === "Deposit") {
        cashData.value[index][0].price = null;
        cashData.value[index][0].depositData.push({
          voucherType: commonDataList.value.voucherTypes[0].id,
          amountOfMoney: null
        });

        return null;
      }

      let deleteAmount = 0;

      if (item.priceType !== "Deposit" && cashData.value[index][0].depositData.length !== 0) {
        cashData.value[index][0].depositData.forEach(item => {
          deleteAmount += item.amountOfMoney;
        });

        total.value -= deleteAmount;
        cashData.value[index][0].depositData = [];

        return null;
      }
    }

    function addDepositInput(index, depositData) {
      const arr = commonDataList.value.voucherTypes.filter(item => !depositData.some(x => x.voucherType === item.id));

      cashData.value[index][0].depositData.push({
        voucherType: arr[0].id,
        amountOfMoney: null
      });
    }

    function deleteDepositInput(index, i) {
      let deleteAmount = cashData.value[index][0].depositData[i].amountOfMoney;

      total.value -= deleteAmount;

      cashData.value[index][0].depositData.splice(i, 1);
    }

    /** 支付方式選擇，新增或刪除預設空值給該部位 */
    function selectedPaymentMethod(item, index, event) {

      const checked = event.target.checked;

      if (item.id === 'Voucher' && !checked) {
        voucherOrderDetailData.value[index] = [];

        oweVoucherInput.value[index].oweAmount = null;
      }

      if (item.id === 'Activity' && checked) {
        activityData.value[index].push(
          {
            prizeId: commonDataList.value.prizeData[0].prizeId,
            bodyPartId: allBodyParts.value[index].id,
            couponNumber: ""
          }
        );
      }

      if (item.id === 'Activity' && !checked) {
        activityData.value[index] = [];
      }

      if (item.id === 'Cash' && !checked) {
        let deleteAmount = 0;

        if (cashData.value[index][0].priceType === 'Deposit') {
          cashData.value[index][0].depositData.forEach(item => {
            deleteAmount += item.amountOfMoney;
          });

          total.value -= deleteAmount;
          cashData.value[index] = [];
          return null;
        }
        
        total.value -= cashData.value[index][0].price;
        cashData.value[index] = [];
      }

      if (item.id === 'Cash' && checked) {
        cashData.value[index].push(
          {
            bodyPartId: allBodyParts.value[index].id,
            paymentMethodId: "Cash",
            priceType: "Once",
            price: null,
            received: false,
            otherPartName: allBodyParts.value[index].part ? allBodyParts.value[index].part : '',
            otherPartScope: allBodyParts.value[index].scope ? allBodyParts.value[index].scope : '',
            depositData: [],
          }
        );
      }
    }


    /** 選擇部位 */
    function selectedBodyParts(item, index, event) {

      const checked = event.target.checked;
      const newNoDataForOtherParts = bodyParts.value.filter(itemA => itemA.id !== 'Other');
      const newNoDataForOtherPartsLen = newNoDataForOtherParts.length - 1;

      if (checked && item.id !== 'Other') {

        const addItem = newNoDataForOtherParts[newNoDataForOtherPartsLen];

        allBodyParts.value.splice(newNoDataForOtherPartsLen, 0, addItem);

        paymentMethodData.value.splice(newNoDataForOtherPartsLen, 0, {
          bodyPartId: addItem.id,
          paymentMethod: [{id: 'Voucher', name: '療程專用券', sequence: 1, active: true}],
        });
        paymentMethodSwitch.value.splice(newNoDataForOtherPartsLen, 0, []);

        activityData.value.splice(newNoDataForOtherPartsLen, 0, []);
        cashData.value.splice(newNoDataForOtherPartsLen, 0, []);

        voucherInput.value.splice(newNoDataForOtherPartsLen, 0, '');
        oweVoucherInput.value.splice(newNoDataForOtherPartsLen, 0, {
          bodyPartId: addItem.id,
          oweAmount: 0
        });

        voucherOrderDetailData.value.splice(newNoDataForOtherPartsLen, 0, []);
      }

      if (!checked && item.id !== 'Other') {
        // 治療部位刪除時
        
        const deleteItem = allBodyParts.value.find(item => {
          return !newNoDataForOtherParts.includes(item);
        });

        const i = allBodyParts.value.indexOf(deleteItem);

        allBodyParts.value.splice(i, 1);
        noDataForOtherParts.splice(i, 1);

        paymentMethodData.value.splice(i, 1);
        paymentMethodSwitch.value.splice(i, 1);

        activityData.value.splice(i, 1);

        let deleteAmount = 0;

        if (cashData.value[i].length !== 0) {
          if (cashData.value[i][0].priceType === 'Deposit') {
          cashData.value[i][0].depositData.forEach(item => {
            deleteAmount += item.amountOfMoney;
          });

            total.value -= deleteAmount;
          } else {
            total.value -= cashData.value[i][0].price;
          }
        }

        cashData.value.splice(i, 1);

        voucherInput.value.splice(i, 1);
        oweVoucherInput.value.splice(i, 1);
        voucherOrderDetailData.value.splice(i, 1);
      }

      if (checked && item.id === 'Other') {
        otherBodyPartLength.value = 1;
        otherBodyParts.value.push({
          part: "",
          scope: "",
          price: ""
        })

        allBodyParts.value.push({
          id: "Other",
          name: "其他",
          part: "",
          price: '',
          scope: ""
        });

        paymentMethodData.value.push({
          bodyPartId: "Other",
          paymentMethod: [{id: 'Cash', name: '現金支付', sequence: 3, active: true}],
        })

        paymentMethodSwitch.value.push({
          bodyPartId: "Other",
          switch: false,
        })

        // activityData.value.push([]);
        cashData.value.push([]);

        // voucherInput.value.push('');
        // voucherOrderDetailData.value.push([]);

        // oweVoucherInput.value.push({
        //   bodyPartId: item.id,
        //   oweAmount: 0
        // });

        cashData.value[cashData.value.length - 1].push(
          {
            bodyPartId: "Other",
            paymentMethodId: "Cash",
            priceType: "Once",
            price: null,
            received: false,
            otherPartName: '',
            otherPartScope: '',
            depositData: [],
          }
        );
      }

      if (!checked && item.id === 'Other') {
        otherBodyPartLength.value = 0;
        otherBodyParts.value = [];
        for(let i = 0; i < allBodyParts.value.length; i++) {
          if (allBodyParts.value[i].id === "Other") {
            allBodyParts.value.splice(i, 1);

            paymentMethodData.value.splice(i, 1);
            paymentMethodSwitch.value.splice(i, 1);

            // activityData.value.splice(i, 1);

            total.value -= cashData.value[i][0].price;
            cashData.value.splice(i, 1);

            // voucherInput.value.splice(i, 1);
            // voucherOrderDetailData.value.splice(i, 1);

            i--;
          }
        }
      }
    }

    /** 其他部位 - 部位 Blur */
    function otherBodyPartInputBlur(index) {
      if (otherBodyParts.value[index].part === '') return null;

      const newNoDataForOtherParts = bodyParts.value.filter(item => item.id !== 'Other');
      const len = newNoDataForOtherParts.length;

      allBodyParts.value[len + index].part = otherBodyParts.value[index].part;

      if (!cashData.value[len + index][0]) return null;

      cashData.value[len + index][0].otherPartName = otherBodyParts.value[index].part;
    }
    
    /** 其他部位 - 範圍 Blur */
    function otherBodyScopeInputBlur(index) {
      if (otherBodyParts.value[index].scope === '') return null;

      const newNoDataForOtherParts = bodyParts.value.filter(item => item.id !== 'Other');
      const len = newNoDataForOtherParts.length;

      allBodyParts.value[len + index].scope = otherBodyParts.value[index].scope;

      if (!cashData.value[len + index][0]) return null;

      cashData.value[len + index][0].scope = otherBodyParts.value[index].scope;
    }

    /** 輸入的金額，計算總金額 和 將輸入的金額補入 otherBodyParts 的 price */
    function cashInputBlur(cashDataItem, index) {
      let added = 0;
      cashData.value.forEach(item => {

        if (item[0]) {
          if (!isNaN(parseInt(item[0].price))) {
            added += parseInt(item[0].price);
            return null;
          }

          if (item[0].depositData.length !== 0) {
            item[0].depositData.forEach(item => {
              if (!isNaN(parseInt(item.amountOfMoney))) {
                added += parseInt(item.amountOfMoney);
              }
            })
          }
        }
      })

      total.value = added + calculateFeeValue;

      if (!cashDataItem) return null;
      if (cashDataItem.bodyPartId === 'Other') {
        const len = bodyParts.value.filter(item => item.id !== 'Other').length;
        const otherBodyIndex = index - len;
        otherBodyParts.value[otherBodyIndex].price = cashData.value[index][0].price;
      }
    }

    /** 登錄 */
    async function logIn() {
      const { initMedicalRecordDetails } = medicalRecordInitData;
      
      // 其他部位
      const otherParts = otherBodyParts.value.map(item => {
        return {
          part: item.part,
          scope: item.scope,
          price: item.price,
        }
      })

      // 是否上麻
      const anesthetic = commonDataList.value.anestheticFees.find(item => item.id === modalData.value.anestheticFee);

      // 現金支付
      const coursePartOrderDetailData = [];

      cashData.value.forEach(item => {
        if (item[0]) {
          coursePartOrderDetailData.push(item[0]);
        }
      })

      // 現金支付 - 驗證

      let coursePartOrderDetailJudge = [];

      cashData.value.forEach((item, index) => {
        if (item.length !== 0 && item[0].priceType !== 'Deposit') {
          if (
            item[0].price === null ||
            item[0].price === '' ||
            item[0].price === 0
          ) {
            coursePartOrderDetailJudge.push(index);
          }
        }
        // 押金驗證
        if (item.length !== 0 && item[0].priceType === 'Deposit') {
          item[0].depositData.forEach(item => {
            if (
              item.amountOfMoney === null ||
              item.amountOfMoney === '' ||
              item.amountOfMoney === 0
            ) {
              coursePartOrderDetailJudge.push(index);
            }
          })
        }
      })

      if (coursePartOrderDetailJudge.length !== 0) {
        updateMessage('現金金額需填寫');
        return null;
      }

      // 支付方式 - 驗證
      if (paymentMethodData.value.length === 0 && modalData.value.courseType !== 'OPD') {
        updateMessage('未選擇部位');
        return null;
      }

      let paymentMethodJudge = [];

      paymentMethodData.value.forEach((item, index) => {
        if (item.paymentMethod.length === 0) {
          paymentMethodJudge.push(index);
        }
      })

      if (paymentMethodJudge.length !== 0) {
        updateMessage('尚有部位未填寫收費方式');
        return null;
      }

      // 欠卷，驗證
      let judge = [];

      paymentMethodData.value.forEach((itemA, index) => {
        itemA.paymentMethod.forEach(itemB => {
          if (itemB.id === 'Voucher') {
            if (oweVoucherInput.value[index].oweAmount === null || oweVoucherInput.value[index].oweAmount === '') {
              judge.push(index);
            }

            if (oweVoucherInput.value[index].oweAmount === 0) {
              if (voucherOrderDetailData.value[index].length === 0) {
                judge.push(index);
              }
            }
          }
        });
      })

      if (judge.length !== 0 && modalData.value.courseType !== 'MicroPlasticSurgery') {
        updateMessage('療程專用券需填入卷號或欠卷數');
        return null;
      }

      // 療程專用券
      const voucherOrderDetail = [];

      voucherOrderDetailData.value.forEach(itemA => {
        if (itemA.length === 0) return null;
        itemA.forEach(itemB => {
          voucherOrderDetail.push(itemB);
        });
      })

      // 療程專用券 - 欠卷
      const oweVoucherSituationData = oweVoucherInput.value.filter(item => item.oweAmount !== 0 && item.oweAmount !== null);

      // 活動優惠券 - 資料整理與驗證
      const couponOrderDetailData = [];
      const couponOrderDetailDataJudge = [];

      activityData.value.forEach((itemA, index) => {
        itemA.forEach(itemB => {
          if (!itemB.couponNumber) {
            couponOrderDetailDataJudge.push(index);
            return null;
          }
          couponOrderDetailData.push(itemB);
        })
      })

      if (couponOrderDetailDataJudge.length !== 0) {
        updateMessage('需填入活動優惠券');
        return null;
      }

      const postData = {
        medicalRecord: {
          ...initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail,
          courseType: modalData.value.courseType,
          courseId: modalData.value.courseId,
          operator: modalData.value.operator,
          beautyConsumableId: modalData.value.beautyConsumableId ? modalData.value.beautyConsumableId : null,
          anesthesiaSituation: modalData.value.anesthesiaSituation,
          id: null,
          comment: modalData.value.comment,
        },
        bodyParts: bodyParts.value.map(item => item.id),
        otherParts: otherParts,
        anestheticOrderDetailData: modalData.value.anesthesiaSituation === 'Anesthesia' ? {
          anesthesiaType: initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.anesthesiaType,
          anestheticFeeId: anesthetic.id,
          price: anesthetic.fee,
          received: anesthetic.id !== 'Free' ? modalData.value.received : true,
        } : null,
        coursePartOrderDetailData: coursePartOrderDetailData,
        voucherOrderDetailData: voucherOrderDetail,
        oweVoucherSituationData,
        couponOrderDetailData,
        notArrivedAppointmentIdList: initMedicalRecordDetails[modalFocusIndex.value].notArrivedAppointmentIdList,
      }

      console.log(postData);

      const url = `/api/MedicalRecord/Arrive`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'POST',
        body: JSON.stringify(postData),
      }

      const response = await fetchApi(url, options);

      updateMessage(response.message);

      if (response.statusCode >= 400) return null;

      const update = {
        medicalRecordId: null,
        customerId: postData.medicalRecord.customerId,
        appointmentId: null,
      }
      await initCheckInModalData(update);
      continuousOrReservedToggle();
    }

    /** 登錄過期判斷 */
    function loginExpirationJudgment() {
      const today = new Date();
      const inputDate = new Date(modalData.value.appointmentDate);

      today.setHours(0, 0, 0, 0);
      inputDate.setHours(0, 0, 0, 0);

      if (today > inputDate) {
        return true;
      } else {
        return false;
      }
    }

    let calculateFeeValue = 0;

    /** 計算麻藥費用 */
    function calculateFee(anestheticFee) {
      const anestheticFeeItem = commonDataList.value.anestheticFees.find(item => item.id === anestheticFee);

      if (calculateFeeValue === 0) {
        total.value += anestheticFeeItem.fee;
      } else {
        total.value -= calculateFeeValue;
        total.value += anestheticFeeItem.fee;
      }

      calculateFeeValue = anestheticFeeItem.fee;
    }

    // --- API ---

    /** 取得療程選項 */
    const getCourseOptions = async(courseType) => {
      const url = `/api/Course/GetCourseOptionsByType?courseTypeId=${courseType}`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      return response.data;
    }

    /** 取得治療部位 */
    const getBodyPart = async(courseId) => {
      const url = `/api/BodyPart/GetBodyPartByCourse?courseId=${courseId}`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      return response.data;
    }

    const updateBasicDataAndFeeData = ref([]);

    /** 更新病歷基本資料和收費 */
    async function updateBasicDataAndFee(customerId, courseId) {
      const url = `/api/MedicalRecord/GetOtherPartHistory?customerId=${customerId}&courseId=${courseId}&limit=5`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      updateBasicDataAndFeeData.value = response.data;
    }

    /** 選擇病歷基本資料和收費 */
    function selectUpdateBasicDataAndFeeData(item) {
      otherBodyPartLength.value++;
      otherBodyParts.value.push({
        part: item.part,
        scope: item.scope,
        price: item.price
      })
    }

    const init = () => {
      stripCalendarInit();
      getClinicPeriodLastTime();
    }

    init();

    return {
      data,
      modalData,
      modalHeaderData,
      modalFocusIndex,
      paymentMethods,
      paymentMethodsForOtherBodyparts,
      paymentMethodSwitch,

      // 登錄
      logIn,
      loginExpirationJudgment,

      chargeContent,
      allBodyParts,
      commonDataList,
      commonDataListForOhterBodypart,

      beautyConsumables,
      selectedPaymentMethod,

      total,
      cashInputBlur,
      changePriceType,
      cashData,
      addDepositInput,
      deleteDepositInput,
      deleteActivityItem,
      activityData,
      
      voucherOrderDetailData,
      voucherDataEnter,

      voucherInput,
      oweVoucherInput,
      
      otherBodyPartInputBlur,
      otherBodyScopeInputBlur,

      addActivityItem,
      deleteVoucherData,
      paymentMethodData,
      openPaymentMethodOption,
      deleteSelectedPaymentMethod,

      selectMedicalRecord,
      coursesOptions,
      bodyPartsOptions,
      bodyParts,
      otherBodyPartLength,
      otherBodyParts,
      bodyPartsWindow,
      changeCourseType,
      changeCourseId,

      monthNumtoEn,
      showDetails,
      closeDetails,
      calendarListMove,
      AestheticMedicineBgJudge,
      beautyBgJudge,
      appointmentBodyPartJudge,
      otherPartNameToArr,
    
      openAppointmentBodyPartsWindow,
      otherBodyPartJudge,
    
      deleteAppointmentBodyPartsItem,
      addOtherBodyPartLength,
      deleteOtherBodyPartLength,
      openHistoryCourseWindow,
      selectDate,
      displayConditions,
      outpatientTimeJudge,
