            <div class="d-flex mb-4">
              <div style="width: 24%;" v-show="checkInContent === 'dateOfAppointment'"></div>
              <button type="button" class="w-100 me-3 bg-white rounded-pill border border-gray"
                      style="font-size: 16px;"
                      @click="switchToDiagnosisOrDetails()">{{ checkInContent === 'dateOfAppointment' ? '詳細資料' : '返回到診表'}}</button>
              <div class="bg-white px-8 py-1 rounded-pill border border-gray">
                <div class="d-flex align-items-center">
                  <div class="text-main-dark1 me-6">
                    <span class="me-1" style="font-size: 20px; font-weight: 700;">{{ month + 1 }}</span>
                    <span style="font-weight: 700;">{{ monthNumtoEn(month) }}</span>
                  </div>
                  <a href="#" class="text-decoration-none d-inline-block me-2" @click.prevent="calendarListMove(true)">
                    <img src="@/assets/stylesheets/icon/arrow_left.png" width="7.62" height="16" alt="arrow_left">
                  </a>
