                    <li class="mx-4 calendarList" v-for="item in data.stripCalendar" :key="item">
                      <a href="#" class="text-decoration-none text-center d-inline-block" @click.prevent="selectDate(item)"
                        :class="{ 'text-gray3' : year !== item.year || month + 1 !== item.month || date !== item.date }">
                        <p class="mb-0 fw-500" style="font-size: 16px;">{{ item.date }}</p>
                        <p class="mb-0 fw-500" style="font-size: 12px;">{{ item.day }}</p>
                      </a>
                    </li>
                  </ul>

                  <a href="#" class="text-decoration-none d-inline-block ms-2" @click.prevent="calendarListMove(false)">
