</template>

<script>
import { inject, toRefs, reactive, onMounted, nextTick, ref } from 'vue';
// import { Modal } from 'bootstrap';

import dateOfAppointment from './dateOfAppointment';
import detailedInformation from './detailedInformation';
export default {
  name: 'appointmentIndex',
  components: {
    detailedInformation,
    dateOfAppointment,
  },
  setup() {
    const checkIn = inject('checkIn');
    const { 
            state,
            getDoctors,
            resetSelectedDoctor,
            getCurrentMonth,
            continuousOrReservedToggle,
            clinicPeriodToCh,
            convertTimeFormatSlash,
            departmentToggle,
            getDaysOutpatientData,
            searchDetailedInformation,
          } = checkIn;

    const fetchFunction = inject('fetchFunction');

    const {
      fetchApi,
      getTokenFromCookie
    } = fetchFunction;

    const data = reactive({
      customer: {
        name: '',
        tel: '',
        birth: '',
        beautician: '',
      },
      focusIndex: null,
      continuousOrReservedStatus: 'continuousOutpatientAmount',
      appointmentBodyPartsWindow: false,
      createCustomerModalSuccess: false,
    });

    /** 醫師陣容 - 選擇醫師頭像 */
    const showOrHideDoctorData = async (item1) => {
      state.doctorData.forEach(item2 => {
        if (item1.id === item2.id) {
          if (item1.select) {
            if (state.selectedDoctor.length === 1) {
              // 最少要 1 位
              return null;
            }
            item1.select = false;
          } else {
            // 最多選 4 位
            if (state.selectedDoctor.length >= 4) {
              return null;
            }
            item1.select = true;
          }
        }
      })

      resetSelectedDoctor();

      if (state.checkInContent === 'detailedInformation') {
        continuousOrReservedToggle(state.calendarContent);
      }

      if (state.checkInContent === 'dateOfAppointment') {
        await getDaysOutpatientData();
      }
    }

    const outstandingData = ref([]);

    /** 取得尚未收費 */
    async function getOutstandingData() {

      const date = `${state.year}-${state.month + 1 <= 9 ?'0':''}${state.month + 1}-${state.date <= 9 ?'0':''}${state.date}`;
  
      const url = `/api/Order/GetOutstandingData?date=${date}`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      outstandingData.value = response.data;
    }


    const switchToDiagnosisOrDetails = () => {
      state.checkInContent = state.checkInContent === 'dateOfAppointment' ? 'detailedInformation' : 'dateOfAppointment';
      continuousOrReservedToggle();
    }

    const monthNumtoEn = (month) => {
      const arr = ["JAN", "FEB", "MAR", "APR", "MAY", "JUN", "JUL", "AUG", "SEP", "OCT", "NOV", "DEC"];
      return arr[month];
    }

    /** 開啟治療部位下拉選單 */
    const openAppointmentBodyPartsWindow = () => {
      if (data.appointmentBodyPartsWindow) return null;
      data.appointmentBodyPartsWindow = true;

      function close() {
        data.appointmentBodyPartsWindow = false;
        document.removeEventListener('click', close);
      }

      document.addEventListener('click', close);
    }

    const setPeriod = (attributes) => {
      const newValue = !state.filterQueryStatus[attributes];

      state.filterQueryStatus[attributes] = newValue;

      continuousOrReservedToggle();
    }

    /** 過濾查詢 - 重置 */
    const filterQueryReset = () => {
      state.filterQueryStatus = {
        continuousOutpatientNum: 1,
        isAppointment: true,
        morning: true,
        afternoon: true,
        evening: true,
      }

      data.continuousOrReservedStatus = 'continuousOutpatientAmount';

      // getDaysOutpatientData();
      continuousOrReservedToggle();
    }
    // --- 手風琴 accordion ---

    /** 手風琴 accordion */
    const accordionNum = ref(0);

    /** 手風琴 accordion 初始 */
    function accordionToggle() {
      if (state.checkInContent !== 'detailedInformation') {
        accordionNum.value = 1;
      }
    }

    onMounted(() => {
      accordionToggle();
    })

    const stripCalendarInit = () => {
      let currentMonth = getStripCalendarData(state.year, state.month);

      currentMonth.forEach((item, index) => {
        if (item.date === state.date) {
          data.focusIndex = index;
        }
      })

      const nextLen = currentMonth.length - 10;
      const lastLen = currentMonth.length - nextLen;

      if (data.focusIndex <= lastLen) {
        const { year, month } = timeSwitch(state.year, state.month, true);
        
        let lastMonth = getStripCalendarData(year, month);
        currentMonth = lastMonth.concat(currentMonth);
      }

      if (data.focusIndex >= nextLen) {
        const { year, month } = timeSwitch(state.year, state.month, false);
        let nextMonth = getStripCalendarData(year, month);
        currentMonth = currentMonth.concat(nextMonth);
      }

      currentMonth.forEach((item, index) => {
        if (item.date === state.date && item.month === state.month + 1 && item.year === state.year) {
          data.focusIndex = index;
        }
      })

      data.stripCalendar = currentMonth;
      
      calendarListFocus(data.focusIndex);
    }

    /** 條狀拖拉日曆移動到目標 */
    const calendarListFocus = (num) => {
     
      const focus = num - 5;

      nextTick(() => {
        const items = document.querySelectorAll('.calendarList');
        let total = 0;

        for (let i = 0; i < focus; i++) {
          total += items[i].offsetWidth + 32;
      }

      const scroll = document.querySelector('.scroll');
      scroll.scrollTo({left: total, behavior: 'smooth'});
      });
    }

    const timeSwitch = (year, month, judge) => {
      if (judge) {
        month -= 1;
      } else {
        month += 1;
      }

      if (month === 12) {
        month = 0;
        year+= 1;
      }

      if (month === -1) {
        month = 11;
        year -= 1;
      }

      return { year, month };
    }

    /** 取得條狀拖拉日曆 */
    const getStripCalendarData = (year, month) => {
      let time = new Date(year, month+1, 1);
      let arr = [];
      let dayEn = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];

      time.setHours(time.getHours() - 1);    // 下個月 - 1 小時，目的是轉換成當月的最後一天 

      const allDate = time.getDate();

      for (let i = 1; i <= allDate; i++) {
        let index = new Date(year, month, i).getDay();
        arr.push({
          day: dayEn[index],
          date: i,
          month: month + 1,
          year: year,
        })
      }

      return arr;
    }

    /** 增加條狀拖拉日曆長度和下個顯示位置 */
    const calendarListMove = (judge) => {
      let len = data.stripCalendar.length;

      const nextLen = len - 10;
      const lastLen = len - nextLen;

      if (judge) {
        data.focusIndex = data.focusIndex - 6;
      } else {
        data.focusIndex = data.focusIndex + 6;
      }

      const focusItem = data.stripCalendar[data.focusIndex];

      if (data.focusIndex <= lastLen) {
        const { year, month } = timeSwitch(data.stripCalendar[data.focusIndex].year, data.stripCalendar[data.focusIndex].month - 1, true);

        let lastMonth = getStripCalendarData(year, month);

        data.stripCalendar = lastMonth.concat(data.stripCalendar);
      }

      if (data.focusIndex >= nextLen) {
        const { year, month } = timeSwitch(data.stripCalendar[data.focusIndex].year, data.stripCalendar[data.focusIndex].month - 1, false);
        let nextMonth = getStripCalendarData(year, month);
        data.stripCalendar = data.stripCalendar.concat(nextMonth);
      }

      data.stripCalendar.forEach((item, index) => {
        if (item.date === focusItem.date && item.month === focusItem.month && item.year === focusItem.year) {
          data.focusIndex = index;
        }
      })
        
      calendarListFocus(data.focusIndex);
    }

    const selectDate = ({date, month, year}) => {
      state.date = date;
      state.month = month - 1;
      state.year = year;

      continuousOrReservedToggle();
    }

    async function init() {
      getCurrentMonth();
      stripCalendarInit();
      await getDoctors();
      resetSelectedDoctor();
      await getDaysOutpatientData();
      await getOutstandingData();
    }

    init();

    return {
      data,
      outstandingData,
      accordionNum,
      showOrHideDoctorData,
      setPeriod,
      clinicPeriodToCh,
      convertTimeFormatSlash,
      openAppointmentBodyPartsWindow,
      departmentToggle,
      searchDetailedInformation,
      filterQueryReset,
      monthNumtoEn,
      calendarListMove,
      selectDate,
      switchToDiagnosisOrDetails,
      ...toRefs(state),
    }
  }
}
</script>

