                                        <input type="number" class="form-control rounded-pill"
                                              v-model="item.price" @blur="cashInputBlur()"
                                              min="0">
                                      </div>
                                    </div>
                                    <div class="col-4">
                                      <div class="input-group">
                                        <select class="rounded-pill form-select text-main-dark2" v-model="item.received">
                                          <option :value="false">費用未收</option>
                                          <option :value="true">費用已收</option>
                                        </select>
                                      </div>
                                    </div>
                                  </div>
                                  <!-- 押金費用時，開啟 -->
                                  <template v-if="item.priceType === 'Deposit'">
                                    <div class="row align-items-center mb-2" v-for="(depositDataItem, i) in item.depositData" :key="i">
                                      <div class="col-4">
                                        <div class="input-group">
                                          <select class="rounded-pill form-select text-main-dark2" v-model="depositDataItem.voucherType">
                                            <template v-for="voucherTypeItem in commonDataList.voucherTypes"
                                                    :key="voucherTypeItem.id">
                                              <option :value="voucherTypeItem.id"
                                                      v-if="voucherTypesJudge(item.depositData, voucherTypeItem.id, depositDataItem.voucherType)">
                                                      {{ voucherTypeItem.name }}
                                              </option>
                                            </template>
                                          </select>

                                          <!-- <select class="rounded-pill form-select text-main-dark2" v-model="depositDataItem.voucherType">
                                            <option v-for="item in commonDataList.voucherTypes"
                                                    :value="item.id"
                                                    :key="item.id">{{ item.name }}
                                            </option>
                                          </select> -->
                                        </div>
                                      </div>
                                      <div class="col-4">
                                        <div class="d-flex align-items-center">
                                          <input type="number" class="form-control rounded-pill"
                                                @blur="cashInputBlur()"
                                                v-model="depositDataItem.amountOfMoney"
                                                min="0">
                                        </div>
                                      </div>
                                      
                                      <div class="col-2">
                                        <a href="#" class="me-2"
                                          @click.prevent="deleteDepositInput(index, i)"
                                          v-if="item.depositData.length !== 1">
                                          <img src="@/assets/stylesheets/icon/--05.png" alt="" width="20" height="20">
                                        </a>

                                        <a href="#" @click.prevent="addDepositInput(index, item.depositData)"
                                          v-if="item.depositData.length === 1 || item.depositData.length === (i + 1) && commonDataList.voucherTypes.length > item.depositData.length">
                                          <img src="@/assets/stylesheets/icon/+-04.png" alt="" width="20" height="20">
                                        </a>
                                      </div>
                                    </div>
                                  </template>         
                                </template>
                              </template>

                            </div>
                          </div>
                        </div>
                      </div>
                    </template>
                  </div>

                  <!-- 其他部位 -->
                  <template v-for="(item, index) in otherBodyParts" :key="item">
                    <div class="ps-7">
                      <p class="fw-500 mb-2">其他 / {{ item.otherPartName }} :</p>
                      <div class="p-0 rounded-15 mb-2 form-select"
                          @click.stop="openOtherbodypartsPaymentMethodOption(index)">
                        <!-- form-control -->
                        <button type="button" class="d-flex flex-wrap border-0 pt-2 pb-0 rounded-15 bg-transparent"
                                style="width: 90%; min-height: 51px;">
                          <button type="button" v-for="item in otherBodyPartPaymentMethodData[index].paymentMethod" :key="`${item.id}${index}`"
                                  @click.stop="deleteOpenOtherbodypartsSelectedPaymentMethod(item, index)"
                                  class="amis-badge amis-badge-second3 me-1 mb-2">
                                  <span class="align-middle me-2">{{ item.name }}</span>
                                  <img class="align-middle" src="@/assets/stylesheets/icon/X-01.png" alt="icon" width="10" height="10">
                          </button>
                        </button>
                      </div>
                      <!-- 支付方式下拉 - 其他部位 -->
                      <div class="bg-gray1 rounded-15 w-100 pt-5 pb-3 px-5 border border-1 border-gray3 mb-2"
                        v-show="otherBodyPartPaymentMethodSwitch[index].switch"
                        data-to="true"
                        @click.stop="null">
                        <div class="scroll-content-none" style="max-height: 170px">
                          <div class="form-check mb-2" v-for="item in paymentMethodsForOtherBodyparts" :key="`${item.id}OtherBodyparts${index}`">
                            <input class="form-check-input" type="checkbox"
                              :value="item"
                              :id="`paymentMethod${item.id}OtherBodyparts${index}`"
                              v-model="otherBodyPartPaymentMethodData[index].paymentMethod"
                              @change="otherBodypartsSelectedPaymentMethod(item, index, $event)">
                            <label class="form-check-label text-main-dark2 d-block"
                                  :for="`paymentMethod${item.id}OtherBodyparts${index}`">
                              {{ item.name }}
                            </label>
                          </div>

                        </div>
                      </div>
                      
                      <div class="ps-2 d-flex w-100"
                          v-for="item in otherBodyPartPaymentMethodData[index].paymentMethod"
                          :key="`${item.id}${index}`">
                        <div class="row w-100 gx-0">
                          <div class="col-2">
                            <div style="padding-top: 7px;">
                              <img src="@\assets\stylesheets\icon\dot-02.png" class="me-2" width="5" height="5" alt="dot-02">
                              <p class="fw-500 mb-0 d-inline pt-3" style="white-space:nowrap;">{{ item.name }}</p>
                            </div>
                          </div>

                          <div class="col-10">
                            <!-- 現金支付 - 其他部位 -->
                            <template v-if="item.id === 'Cash'">
                              <div class="row align-items-center mb-2" v-for="(item, i) in otherBodyPartCashData[index]" :key="i">
                                <div class="col-4">
                                  <div class="input-group">
                                    <select class="rounded-pill form-select text-main-dark2" v-model="item.priceType">
                                      <option v-for="item in commonDataListForOhterBodypart.priceTypes"
                                              :value="item.id"
                                              :key="item.id">{{ item.name }}
                                      </option>
                                    </select>
                                  </div>
                                </div>
                                <div class="col-4">
                                  <div class="d-flex align-items-center">
                                    <input type="number" class="form-control rounded-pill"
                                          v-model="item.price" @blur="cashInputBlur()"
                                          min="0">
                                  </div>
                                </div>
                                <div class="col-4">
                                  <div class="input-group">
                                    <select class="rounded-pill form-select text-main-dark2" v-model="item.received">
                                      <option :value="false">費用未收</option>
                                      <option :value="true">費用已收</option>
                                    </select>
                                  </div>
                                </div>
                              </div>
                            </template>

                          </div>
                        </div>
                      </div>
                    </div>
                  </template>
                  
                </div>
            </div>


            <!-- modal footer -->
            <div class="row gx-0">
              <div class="col-12 py-4 px-10 border-2 border-top border-gray1">
                <div class="d-flex align-items-center">
                  <label class="me-10" for="">總金額</label>
                  <input type="text" class="form-control w-auto d-inline-block rounded-pill me-5" placeholder="0"
                        v-model="total">

                  <div class="form-check d-inline-block me-5">
                    <input class="form-check-input" type="checkbox" id="requestMedicalReceipt">
                    <label class="form-check-label" for="requestMedicalReceipt">
                      索取醫療收據
                    </label>
                  </div>

                  <div class="form-check d-inline-block me-auto">
                    <input class="form-check-input" type="checkbox" value="" id="printReceipt">
                    <label class="form-check-label" for="printReceipt">
                      列印收據
                    </label>
                  </div>

                  <div class="d-inline-block">
                    <button type="button" class="btn btn-alert-second rounded-pill me-2" data-bs-dismiss="modal">關 閉</button>
                    <button type="button" class="btn btn-alert-main rounded-pill" @click="editCompleted()">修改完成</button>
                  </div>
                  
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import { Modal } from 'bootstrap';
// import { inject, toRefs, reactive, nextTick, computed, watch, onMounted, ref } from 'vue';
import { inject, onMounted, ref, reactive, toRefs } from 'vue';

export default {
  name: 'detailedInformation',
  setup() {
    const editCheckInModalRef = ref(null);
    const data = reactive({
      loginLock: false,
    })

    const checkIn = inject('checkIn');
    // const alertMessage = inject('alertMessage');
    const textConversionTool = inject('textConversionTool');
    const { 
      state,
      detailedinformationData,
      continuousOrReservedToggle,
      // getDoctorAttendanceData,
    } = checkIn;

    const fetchFunction = inject('fetchFunction');

    const {
      fetchApi,
      getTokenFromCookie
    } = fetchFunction;

    const alertMessage = inject('alertMessage');

    const { updateMessage } = alertMessage;

    const {
      deleteTTimeFormat,
      convertTimeFormatSlash,
      clinicPeriodToCh,
      convertTimeFormatDeleteSecond,
    } = textConversionTool;

    let editCheckInModal;

    onMounted(() => {
      editCheckInModal = new Modal(editCheckInModalRef.value);
    })

    /** 開啟編輯收費 modal */
    const openEditCheckInModal = (id) => {
      modalFocusIndex.value = 0;
      editCheckInModal.show();
      getInitUpdateMedicalRecordFeeData(id);
    }

    /** 燈號判斷 */
    function lightsJudge(item) {
      const leftTimeJudge = item.attendanceMedicalRecord.every(itemA => {
        return itemA.detail.every(itemB => itemB.cureContent.leftTime);
      })

      // const endTimeOfTreatmentJudge = item.followUpMedicalRecord.some(itemA => {
      //   return itemA.detail.some(itemB => itemB.cureContent.endTimeOfTreatment);
      // })

      if (leftTimeJudge) {
        return 'bg-gray3'
      }
      
      // if (endTimeOfTreatmentJudge) {
      //   return 'bg-mark'
      // }

      return 'bg-green'
    }

    /** 取得編輯收費初始資料 */
    async function getInitUpdateMedicalRecordFeeData(id) {

      const date = `${state.year}-${state.month + 1 <= 9 ?'0':''}${state.month + 1}-${state.date <= 9 ?'0':''}${state.date}`;

      let url = `/api/MedicalRecord/GetInitUpdateMedicalRecordFeeData?departmentId=${state.department}&customerId=${id}&date=${date}`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      updateMedicalRecordFeeDataInit(response.data);
    }

    /** modal header - 選擇病歷 */
    function selectMedicalRecord(id, index) {
      modalFocusIndex.value = index;
      getInitUpdateMedicalRecordFeeData(id);
    }

    const modalData = ref({});
    const modalFocusIndex = ref(0);

    /** 總金額  */
    const total = ref(0);

    /** 病歷用的共用資料 */
    const commonDataList = ref({});

    /** 病歷用的共用資料 (其他部位) */
    const commonDataListForOhterBodypart = ref({});

    /** 美容耗材下拉 */
    const beautyConsumables = ref([]);

    const allBodyParts = ref([]);

    const otherBodyParts = ref([]);

    /** 支付方式資料 */
    const paymentMethodData = ref([]);

    const otherBodyPartPaymentMethodData = ref([]);

    /** 支付方式下拉開關 */
    const paymentMethodSwitch = ref([]);

    const otherBodyPartPaymentMethodSwitch = ref([]);

    /** 療程專用券 - 資料 */
    const voucherOrderDetailData = ref([]);

    /** 支付方式選項 */
    const paymentMethods = ref([]);

    /** 支付方式選項 - 其他部位 */
    const paymentMethodsForOtherBodyparts = ref([]);

    const activityData = ref([]);

    const cashData = ref([]);

    const otherBodyPartCashData = ref([]);

    const voucherInput = ref([]);

    const oweVoucherInput = ref([]);

    /** 編輯收費 - 表頭 */
    const modalHeaderData = ref([]);

    /** 編輯收費初始整理 */
    function updateMedicalRecordFeeDataInit(response) {

      const { customer, initMedicalRecordDetails, commonData } = response;

      commonDataList.value = commonData;
      commonDataListForOhterBodypart.value = JSON.parse(JSON.stringify(commonData));

      commonDataListForOhterBodypart.value.priceTypes = commonDataListForOhterBodypart.value.priceTypes.filter(item => item.id === 'Once' );

      // 複製療程種類 - 用於 modal header 初始
      const courseTypesData = JSON.parse(JSON.stringify(commonDataList.value.courseTypes));

      // modal 初始資料
      modalData.value = {
        name: customer.name,
        birthday: customer.birthday,
        beauticianNickname: customer.beauticianNickname,
        appointmentDate: initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.appointmentDate,
        appointmentTime: initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.appointmentTime,
        clinicPeriod: initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.clinicPeriod,
        arrivedTime: initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.arrivedTime,
        operator: initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.operator,
        courseType: initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.courseType,
        courseId: initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.courseId,
        comment: initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.comment,
        anesthesiaSituation: initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.anesthesiaSituation,
        anestheticFee: initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.anesthesiaSituation === 'Anesthesia' ? initMedicalRecordDetails[modalFocusIndex.value].anestheticOrderDetail.anestheticFeeId : null,
        received: initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.anesthesiaSituation === 'Anesthesia' ? initMedicalRecordDetails[modalFocusIndex.value].anestheticOrderDetail.received : null,
        anesthesiaType: initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.anesthesiaType,
        beautyConsumableId: initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.beautyConsumableId,
        medicalRecordId: initMedicalRecordDetails[modalFocusIndex.value].medicalRecordDetail.id,
      }

      // modal header 初始
      modalHeaderData.value = initMedicalRecordDetails.map(item1 => {
        const courseType = courseTypesData.find(item2 => item2.id === item1.medicalRecordDetail.courseType).name;
        const course = item1.courses.find(item2 => item2.courseId === item1.medicalRecordDetail.courseId).courseName;

        return {
          courseType,
          course,
          medicalRecordId: item1.medicalRecordDetail.id,
          customerId: customer.id,
          // appointmentId: item1.notArrivedAppointmentIdList[0] ? item1.notArrivedAppointmentIdList[0] : null,
        }
      })

      // 美容耗材下拉
      beautyConsumables.value = initMedicalRecordDetails[modalFocusIndex.value].beautyConsumables;

      // 治療部位
      const bodyParts = initMedicalRecordDetails[modalFocusIndex.value].bodyPartsOptions.filter(item => initMedicalRecordDetails[modalFocusIndex.value].bodyParts.includes(item.id));

      // 篩出 'Other'
      const noDataForOtherParts = bodyParts.filter(item => item.id !== 'Other');

      // 美容部部位
      if (state.department === 'Beauty') {
        noDataForOtherParts.push({ id: '1', name: '全臉' });
      }

      // 點滴
      if (modalData.value.courseType === 'IV') {
        noDataForOtherParts.push({ id: null, name: '點滴' });
      }

      // 支付方式初始
      paymentMethodData.value = [];
      paymentMethodSwitch.value = [];

      activityData.value = [];
      cashData.value = [];
      voucherInput.value = [];
      oweVoucherInput.value = [];
      voucherOrderDetailData.value = [];

      // 其他部位 - 現金支付初始

      otherBodyParts.value = [];

      otherBodyPartCashData.value = [];

      otherBodyPartPaymentMethodSwitch.value = [];

      otherBodyPartPaymentMethodData.value = [];

      // let otherBodyPartsCopy = initMedicalRecordDetails[modalFocusIndex.value].otherParts.map(item => {
      //   return {
      //     ...item,
      //     id: "Other",
      //     name: "其他",
      //   }
      // })

      // 治療部位 和 其他部位合併
      // allBodyParts.value = [...noDataForOtherParts, ...otherBodyPartsCopy];
      allBodyParts.value = [...noDataForOtherParts];

      // 支付方式列表
      paymentMethods.value = initMedicalRecordDetails[modalFocusIndex.value].paymentMethods;

      // 支付方式列表 - 其他部位
      paymentMethodsForOtherBodyparts.value = JSON.parse(JSON.stringify(initMedicalRecordDetails[modalFocusIndex.value].paymentMethods));
      paymentMethodsForOtherBodyparts.value = paymentMethodsForOtherBodyparts.value.filter(item => item.id === 'Cash' || item.id === 'CreditCard' || item.id === 'Free')

      allBodyParts.value.forEach(item => {

        paymentMethodData.value.push({
          bodyPartId: item.id,
          paymentMethod: [],
        })
        
        paymentMethodSwitch.value.push({
          bodyPartId: item.id,
          switch: false,
        })

        activityData.value.push([]);
        cashData.value.push([]);

        voucherInput.value.push('');
        oweVoucherInput.value.push({
          bodyPartId: item.id,
          oweAmount: 0
        });
        
        voucherOrderDetailData.value.push([]);
      });

      // 療程專用券 - 預設
      initMedicalRecordDetails[modalFocusIndex.value].voucherOrderDetails.forEach(itemA => {
        allBodyParts.value.forEach((itemB, index) => {
          if (itemA.bodyPartId === itemB.id) {
            voucherOrderDetailData.value[index].push(itemA);

            const voucherPaymentMethod = {id: 'Voucher', name: '療程專用券', sequence: 1, active: true};

            const judge = paymentMethodData.value[index].paymentMethod.some(item => item.id === 'Voucher');

            if (!judge) {
              paymentMethodData.value[index].paymentMethod.push(voucherPaymentMethod);
            }

          }
        })
      })

      initMedicalRecordDetails[modalFocusIndex.value].oweVoucherSituationData.forEach(itemA => {
        allBodyParts.value.forEach((itemB, index) => {
          if (itemA.bodyPartId === itemB.id) {

            oweVoucherInput.value[index].oweAmount = itemA.oweAmount;


            const voucherPaymentMethod = {id: 'Voucher', name: '療程專用券', sequence: 1, active: true};

            const judge = paymentMethodData.value[index].paymentMethod.some(item => item.id === 'Voucher');

            if (!judge) {
              paymentMethodData.value[index].paymentMethod.push(voucherPaymentMethod);
            }

          }
        })
      })

      // 活動優惠卷 - 預設
      initMedicalRecordDetails[modalFocusIndex.value].couponOrderDetails.forEach(itemA => {
        allBodyParts.value.forEach((itemB, index) => {
          if (itemA.bodyPartId === itemB.id) {

            activityData.value[index].push(
              {
                prizeId: itemA.prizeId,
                bodyPartId: itemA.bodyPartId,
                couponNumber: itemA.couponNumber
              }
            );

            const activityPaymentMethod = { id: 'Activity', name: '活動優惠券', sequence: 2, active: true };

            const judge = paymentMethodData.value[index].paymentMethod.some(item => item.id === 'Activity');

            if (!judge) {
              paymentMethodData.value[index].paymentMethod.push(activityPaymentMethod);
            }

          }
        })
      })

      // 現金支付 - 預設
      initMedicalRecordDetails[modalFocusIndex.value].coursePartOrderDetails.forEach(itemA => {
        allBodyParts.value.forEach((itemB, index) => {

          if (itemA.bodyPartId === itemB.id && itemA.bodyPartId !== 'Other') {

            cashData.value[index].push(itemA);

            const cashPaymentMethod = {id: 'Cash', name: '現金支付', sequence: 3, active: true};

            const judge = paymentMethodData.value[index].paymentMethod.some(item => item.id === 'Cash');

            if (!judge) {
              paymentMethodData.value[index].paymentMethod.push(cashPaymentMethod);
            }
          }
        })
      })

      // 現金支付 - 其他部位 - 預設
      initMedicalRecordDetails[modalFocusIndex.value].coursePartOrderDetails.forEach(itemA => {
        if (itemA.bodyPartId === 'Other') {

          otherBodyParts.value.push(itemA);

          otherBodyPartCashData.value.push([]);

          otherBodyPartCashData.value[otherBodyPartCashData.value.length - 1].push(itemA);

          otherBodyPartPaymentMethodSwitch.value.push({
          // bodyPartId: item.id,
          switch: false,
        })

          otherBodyPartPaymentMethodData.value.push({
            // bodyPartId: item.id,
            paymentMethod: [{id: 'Cash', name: '現金支付', sequence: 3, active: true}],
          })
        }
      })

      cashInputBlur();
    }

    /** 開啟支付方式下拉選項 */
    function openPaymentMethodOption(index) {
      if (paymentMethodSwitch.value[index].switch) return null;
      
      let indexClicked;

      // 取得被展開過的優惠卷下拉視窗
      const judge = paymentMethodSwitch.value.find((item, i) => {
        if (item.switch) {
          indexClicked = i;
          return item;
        }
      })

      // 有被展開過，就關閉該視窗
      if (judge) {
        paymentMethodSwitch.value[indexClicked].switch = false;
        document.removeEventListener('click', close);
      }

      paymentMethodSwitch.value[index].switch = true;

      function close() {
        paymentMethodSwitch.value[index].switch = false;
        document.removeEventListener('click', close);
      }

      document.addEventListener('click', close);
    }

    /** 刪除已選的支付方式 */
    function deleteSelectedPaymentMethod(item, index) {

      const i = paymentMethodData.value[index].paymentMethod.indexOf(item);
      paymentMethodData.value[index].paymentMethod.splice(i, 1);

      if (item.id === 'Cash') {
        let deleteAmount = 0;

        if (cashData.value[index][0].priceType === 'Deposit') {
          cashData.value[index][0].depositData.forEach(item => {
            deleteAmount += item.amountOfMoney;
          });

          total.value -= deleteAmount;
          cashData.value[index] = [];
          return null;
        }

        total.value -= cashData.value[index][0].price;
        cashData.value[index] = [];
      }

      if (item.id === 'Voucher') {
        voucherOrderDetailData.value[index] = [];

        oweVoucherInput.value[index].oweAmount = 0;
      }

      if (item.id === 'Activity') {
        activityData.value[index] = [];
      }
    }

    /** 開啟其他部位的支付方式下拉選項 */
    function openOtherbodypartsPaymentMethodOption(index) {
      if (otherBodyPartPaymentMethodSwitch.value[index].switch) return null;
      let indexClicked;

      // 取得被展開過的優惠卷下拉視窗
      const judge = otherBodyPartPaymentMethodSwitch.value.find((item, i) => {
        if (item.switch) {
          indexClicked = i;
          return item;
        }
      })

      // 有被展開過，就關閉該視窗
      if (judge) {
        otherBodyPartPaymentMethodSwitch.value[indexClicked].switch = false;
        document.removeEventListener('click', close);
      }

      otherBodyPartPaymentMethodSwitch.value[index].switch = true;

      function close() {
        otherBodyPartPaymentMethodSwitch.value[index].switch = false;
        document.removeEventListener('click', close);
      }

      document.addEventListener('click', close);
    }

    /** 刪除其他部位已選的支付方式 */
    function deleteOpenOtherbodypartsSelectedPaymentMethod(item, index) {

      const i = otherBodyPartPaymentMethodData.value[index].paymentMethod.indexOf(item);
      otherBodyPartPaymentMethodData.value[index].paymentMethod.splice(i, 1);

      if (item.id === 'Cash') {
        total.value -= otherBodyPartCashData.value[index][0].price;
        otherBodyPartCashData.value[index] = [];
      }
    }

    /** 支付方式選擇，新增或刪除預設空值給該部位 */
    function selectedPaymentMethod(item, index, event) {

      const checked = event.target.checked;

      if (item.id === 'Voucher' && !checked) {
        voucherOrderDetailData.value[index] = [];

        oweVoucherInput.value[index].oweAmount = 0;
      }

      if (item.id === 'Activity' && checked) {
        activityData.value[index].push(
          {
            prizeId: commonDataList.value.prizeData[0].prizeId,
            bodyPartId: allBodyParts.value[index].id,
            couponNumber: ""
          }
        );
      }

      if (item.id === 'Activity' && !checked) {
        activityData.value[index] = [];
      }

      if (item.id === 'Cash' && !checked) {
        let deleteAmount = 0;

        if (cashData.value[index][0].priceType === 'Deposit') {
          cashData.value[index][0].depositData.forEach(item => {
            deleteAmount += item.amountOfMoney;
          });

          total.value -= deleteAmount;
          cashData.value[index] = [];
          return null;
        }
        
        total.value -= cashData.value[index][0].price;
        cashData.value[index] = [];
      }

      if (item.id === 'Cash' && checked) {
        cashData.value[index].push(
          {
            bodyPartId: allBodyParts.value[index].id,
            paymentMethodId: "Cash",
            priceType: "Once",
            price: null,
            received: false,
            otherPartName: allBodyParts.value[index].part ? allBodyParts.value[index].part : '',
            otherPartScope: allBodyParts.value[index].scope ? allBodyParts.value[index].scope : '',
            depositData: [],
          }
        );
      }
    }

    /** 其他部位支付方式選擇，新增或刪除預設空值給該部位 */
    function otherBodypartsSelectedPaymentMethod(item, index, event) {

      const checked = event.target.checked;

      if (item.id === 'Cash' && !checked) {
        total.value -= otherBodyPartCashData.value[index][0].price;
        otherBodyPartCashData.value[index] = [];
      }

      if (item.id === 'Cash' && checked) {
        otherBodyPartCashData.value[index].push(
          {
            bodyPartId: "Other",
            paymentMethodId: "Cash",
            priceType: "Once",
            price: null,
            received: false,
            otherPartName: otherBodyParts.value[index].part ? otherBodyParts.value[index].part : null,
            otherPartScope: otherBodyParts.value[index].scope ? otherBodyParts.value[index].scope : null,
          }
        );
      }
    }

    // --- 療程專用券 ---

    /** 療程專用券 - 輸入 */
    function voucherDataEnter(index) {
      voucherOrderDetailData.value[index].push({
        bodyPartId: allBodyParts.value[index].id,
        voucherId: voucherInput.value[index],
      });

      voucherInput.value[index] = '';
    }

    /** 療程專用券 - 刪除 */
    function deleteVoucherData(item, index) {
      const i = voucherOrderDetailData.value[index].indexOf(item);

      voucherOrderDetailData.value[index].splice(i, 1);
    }

    // --- 活動優惠券 ---

    /** 刪除活動優惠券的券號欄位 */
    function deleteActivityItem(index, i) {
      activityData.value[index].splice(i, 1);
    }

    /** 新增活動優惠券的券號欄位 */
    function addActivityItem(index) {
      activityData.value[index].push(
        {
          prizeId: commonDataList.value.prizeData[0].prizeId,
          bodyPartId: allBodyParts.value[index].id,
          couponNumber: ""
        }
      );
    }

    /** 改變現金支付種類 */
    function changePriceType(item, index) {
      total.value -= item.price;
      cashData.value[index][0].price = null;

      if (item.priceType === "Deposit") {
        cashData.value[index][0].price = null;
        cashData.value[index][0].depositData = [];
        cashData.value[index][0].depositData.push({
          voucherType: commonDataList.value.voucherTypes[0].id,
          amountOfMoney: null
        });

        return null;
      }

      let deleteAmount = 0;

      if (item.priceType !== "Deposit" && cashData.value[index][0].depositData.length !== 0) {
        cashData.value[index][0].depositData.forEach(item => {
          deleteAmount += item.amountOfMoney;
        });

        total.value -= deleteAmount;
        cashData.value[index][0].depositData = [];

        return null;
      }
    }

    function addDepositInput(index, depositData) {
      const arr = commonDataList.value.voucherTypes.filter(item => !depositData.some(x => x.voucherType === item.id));

      cashData.value[index][0].depositData.push({
        voucherType: arr[0].id,
        amountOfMoney: null
      });
    }

    function deleteDepositInput(index, i) {
      let deleteAmount = cashData.value[index][0].depositData[i].amountOfMoney;

      total.value -= deleteAmount;

      cashData.value[index][0].depositData.splice(i, 1);
    }

    function voucherTypesJudge(depositData, id, voucherType) {
      if (voucherType === id) return true;

      return !depositData.some(item => item.voucherType === id);
    }

    /** 修改 "是否上麻" 後的初始 */
    function changeAnesthesiaSituation() {
      modalData.value.anestheticFee = commonDataList.value.anestheticFees[0].id;
      modalData.value.received = modalData.value.courseType === 'MicroPlasticSurgery' ? true : '';
    }

    /** 輸入的金額，計算總金額 和 將輸入的金額補入 otherBodyParts 的 price */
    async function cashInputBlur() {
      let added = 0;
      cashData.value.forEach(item => {
        if (item[0]) {
          if (!isNaN(parseInt(item[0].price)) && item[0].depositData.length === 0) {
            added += parseInt(item[0].price);
          }

          if (item[0].depositData.length !== 0) {
            item[0].depositData.forEach(item => {
              if (!isNaN(parseInt(item.amountOfMoney))) {
                added += parseInt(item.amountOfMoney);
              }
            })
          }
        }
      })

      otherBodyPartCashData.value.forEach(item => {

        if (item[0]) {
          if (!isNaN(parseInt(item[0].price))) {
            added += parseInt(item[0].price);
            return null;
          }
        }
      })

      total.value = added;
    }

    async function editCompleted() {

      // 是否上麻
      const anesthetic = commonDataList.value.anestheticFees.find(item => item.id === modalData.value.anestheticFee);

      // 現金支付
      const coursePartOrderDetailData = [];

      cashData.value.forEach(item => {
        if (item[0]) {
          coursePartOrderDetailData.push(item[0]);
        }
      })

      otherBodyPartCashData.value.forEach(item => {
        if (item[0]) {
          coursePartOrderDetailData.push(item[0]);
        }
      })

      // 欠卷，驗證
      let judge = [];

      paymentMethodData.value.forEach((itemA, index) => {
        itemA.paymentMethod.forEach(itemB => {
          if (itemB.id === 'Voucher') {
            if (oweVoucherInput.value[index].oweAmount === null || oweVoucherInput.value[index].oweAmount === '') {
              judge.push(index);
            }

            if (oweVoucherInput.value[index].oweAmount === 0) {
              if (voucherOrderDetailData.value[index].length === 0) {
                judge.push(index);
              }
            }
          }
        });
      })

      if (judge.length !== 0) return null;

      // 療程專用券
      const voucherOrderDetail = [];

      voucherOrderDetailData.value.forEach(itemA => {
        if (itemA.length === 0) return null;
        itemA.forEach(itemB => {
          voucherOrderDetail.push(itemB);
        });
      })

      // 療程專用券 - 欠卷
      const oweVoucherSituationData = oweVoucherInput.value.filter(item => item.oweAmount !== 0);

      // 活動優惠券
      const couponOrderDetailData = [];

      activityData.value.forEach(itemA => {
        itemA.forEach(itemB => {
          if (!itemB.couponNumber) return null;
          couponOrderDetailData.push(itemB);
        })
      })     

      const putData = {
        medicalRecordId: modalData.value.medicalRecordId,
        medicalRecordComment: modalData.value.comment,
        beautyConsumableId: modalData.value.beautyConsumableId,
        anesthesiaSituation: modalData.value.anesthesiaSituation,
        anestheticOrderDetailData: modalData.value.anesthesiaSituation === 'Anesthesia' ? {
          anesthesiaType: modalData.value.anesthesiaType,
          anestheticFeeId: anesthetic.id,
          price: anesthetic.fee,
          received: anesthetic.id !== 'Free' ? modalData.value.received : true,
        } : null,
        coursePartOrderDetailData: coursePartOrderDetailData,
        voucherOrderDetailData: voucherOrderDetail,
        oweVoucherSituationData,
        couponOrderDetailData,
      }

      const url = `/api/MedicalRecord/UpdateFee`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'PUT',
        body: JSON.stringify(putData),
      }

      const response = await fetchApi(url, options);

      updateMessage(response.message);

      if (response.statusCode >= 400) return null;

      continuousOrReservedToggle();
    }

    /** 初始 */
    // function init() {
    //   getDoctorAttendanceData();
    // }

    // init();

    return {
      data,
      detailedinformationData,
      modalData,
      allBodyParts,
      otherBodyParts,
      beautyConsumables,
      commonDataList,
      commonDataListForOhterBodypart,

      paymentMethodData,
      otherBodyPartPaymentMethodData,

      paymentMethodSwitch,
      otherBodyPartPaymentMethodSwitch,
      openOtherbodypartsPaymentMethodOption,
      deleteOpenOtherbodypartsSelectedPaymentMethod,
      otherBodypartsSelectedPaymentMethod,
      modalHeaderData,
      modalFocusIndex,
      
      activityData,
      cashData,
      otherBodyPartCashData,
      editCompleted,
      selectMedicalRecord,
      lightsJudge,

      voucherInput,
      oweVoucherInput,
      voucherOrderDetailData,
      paymentMethods,
      paymentMethodsForOtherBodyparts,
      deleteSelectedPaymentMethod,
      selectedPaymentMethod,
      deleteActivityItem,
      addActivityItem,
      voucherDataEnter,
      deleteVoucherData,
      changePriceType,
      addDepositInput,
      voucherTypesJudge,

      deleteDepositInput,
      cashInputBlur,
      total,
      changeAnesthesiaSituation,

      openPaymentMethodOption,
      openEditCheckInModal,
      editCheckInModalRef,
      deleteTTimeFormat,
      convertTimeFormatSlash,
      clinicPeriodToCh,
      convertTimeFormatDeleteSecond,
      ...toRefs(state),
    }
  },
}
</script>

